import Component from "../react/component";

// æŒ‚è½½åˆ›å»ºçš„èŠ‚ç‚¹åˆ°æ ¹èŠ‚ç‚¹ä¸Š
function render (vnode, container) {
    return container.appendChild(_render(vnode));
}

// è¿”å›åˆ›å»ºçš„ DOM èŠ‚ç‚¹
function _render (vnode) {
    if (vnode === undefined) return;

    if (typeof vnode === 'number') vnode = String(vnode);

    // å­—ç¬¦ä¸²åˆ™åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹
    if (typeof vnode === 'string') {
        return document.createTextNode(vnode);
    }

    // å‡½æ•°åˆ™åˆ›å»ºç»„ä»¶
    if (typeof vnode.tag === 'function') {
        // 1. åˆ›å»ºç»„ä»¶
        const comp = createComponent(vnode.tag, vnode.attrs);
        // 2. è®¾ç½®ç»„ä»¶ props
        setComponentProps(comp, vnode.attrs);
        // 3. æ¸²æŸ“ç»„ä»¶
        renderComponent(comp);
        // 4. è¿”å›ç»„ä»¶èŠ‚ç‚¹å¯¹è±¡
        return comp.base;
    }

    // è™šæ‹Ÿ DOM å¯¹è±¡åˆ™åˆ›å»ºå¯¹åº”èŠ‚ç‚¹
    const { tag, attrs, childrens } = vnode;
    const dom = document.createElement(tag); // åˆ›å»ºèŠ‚ç‚¹å¯¹è±¡

    if (attrs) {
        Object.keys(attrs).forEach(key => {
            const value = attrs[key];
            setAttribute(dom, key, value);
        })
    }

    if (childrens) {
        childrens.forEach(child => render(child, dom));
    }

    return dom;
}

// é‡å†™ setAttribute æ–¹æ³•ï¼ŒclassName style ç­‰å¤„ç†
function setAttribute (dom, key, value) {
    if (key === 'className') { // å°† className è½¬æˆ class
        key = 'class';
        dom.setAttribute(key, value);
    }

    if (/on\w+/.test(key)) { // äº‹ä»¶
        key = key.toLowerCase();
        dom[key] = value;
    } else if (key === 'style') { // æ ·å¼
        if (!value || typeof value === 'string') {
            dom.style.cssText = value;
        } else if (value && typeof value === 'object') {
            for (let k in value) {
                if (typeof value[k] === 'number') {
                    dom.style[k] = value[k] + 'px';
                } else {
                    dom.style[k] = value[k];
                }
            }
        }
    } else { // å…¶ä»–å±æ€§
        if (key in dom) {
            dom[key] = value || '';
        }
        if (value) {
            dom.setAttribute(key, value);
        } else {
            dom.removeAttribute(key);
        }
    }
}

// åˆ›å»ºç»„ä»¶
function createComponent (comp, props) {
    let inst;
    if (comp.prototype && comp.prototype.render) { // ç±»ç»„ä»¶åˆ›å»ºå®ä¾‹è¿”å›
        inst = new comp(props);
    } else { // å‡½æ•°ç»„ä»¶åˆ™å°†å…¶æ‰©å±•æˆç±»ç»„ä»¶
        inst = new Component(props);
        inst.constructor = comp; // ä¿®æ”¹å®ä¾‹æ„é€ å‡½æ•°
        inst.render = function () { // å®šä¹‰ render å‡½æ•°
            return this.constructor();
        }
    }
    return inst;
}

// è®¾ç½®ç»„ä»¶ props
function setComponentProps (comp, props) {
    // ç”Ÿå‘½å‘¨æœŸ
    if (!comp.base) {
        if (comp.componentWillMount) comp.componentWillMount();
    } else if (comp.componentWillReceiveProps) { // åˆæ¬¡æŒ‚è½½ä¸ä¼šæ‰§è¡Œï¼Œæ›´æ–°æ—¶æ¥å— props å‰æ‰§è¡Œ
        comp.componentWillReceiveProps();
    }

    comp.props = props;
} 

// æ¸²æŸ“ç»„ä»¶
export function renderComponent (comp) {
    let base;
    let isMounted =  comp.base; // ç»„ä»¶æ˜¯å¦å·²æŒ‚è½½
    const renderer = comp.render(); // jsx å¯¹è±¡
    base = _render(renderer); // DOM å¯¹è±¡

    // ç”Ÿå‘½å‘¨æœŸ
    if (isMounted && comp.componentWillUpdate) {
        comp.componentWillUpdate();
    }

    // æ›´æ–°æ—¶èŠ‚ç‚¹æ›¿æ¢
    if (comp.base && comp.base.parentNode) {
        comp.base.parentNode.replaceChild(base, comp.base);
    }

    comp.base = base;

    if (isMounted) {
        if (comp.componentDidUpdate) comp.componentDidUpdate();
    } else if (comp.componentDidMount) {
        comp.componentDidMount();
    }
}

export default {
    render
}